import { json, redirect } from "@remix-run/node";
import { Form, useActionData, useTransition } from "@remix-run/react";
import { getSession, commitSession } from "~/sessions.server";
import { useEffect } from "react";

export async function action({ request }) {
  const formData = await request.formData();
  const email = formData.get("email");
  const password = formData.get("password");

  try {
    const response = await fetch("http://192.168.1.51:8056/auth/login", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ email, password }),
    });

    if (!response.ok) {
      const errorText = await response.text();
      console.error("❌ Login failed:", errorText);
      return json({ error: "Invalid email or password" }, { status: 401 });
    }

    const apiData = await response.json();
    const userData = apiData.data.data;

    const session = await getSession(request);
    session.set("user", userData);

    return redirect("/dashboard", {
      headers: {
        "Set-Cookie": await commitSession(session),
      },
    });
  } catch (error) {
    console.error("💥 Login failed:", error);
    return json({ error: "Server error. Try again later." }, { status: 500 });
  }
}

export default function Login() {
  const actionData = useActionData();
  const transition = useTransition();

  useEffect(() => {
    if (actionData?.error) {
      alert(actionData.error); // You can replace this with toast later
    }
  }, [actionData]);

  const isSubmitting = transition.state === "submitting";

  return (
    <div className="login-page">
      <h2>Login</h2>
      <Form method="post">
        <label>Email:</label>
        <input type="email" name="email" required />
        <label>Password:</label>
        <input type="password" name="password" required />

        <button type="submit" disabled={isSubmitting}>
          {isSubmitting ? "Logging in..." : "Login"}
        </button>
      </Form>
    </div>
  );
}
